<template>
  <div>
    <el-dialog
      width="95%"
      title="查看收货单"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="cancel">
      <el-form inline ref="orderInfo" :model="orderInfo">
        <el-row>
          <el-col :span="6">
            <el-form-item label="商户:" label-width="60px">
              <span>{{orderInfo.goodsReceiveMerchant}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="送达时间:" label-width="100px">
              <span>{{formatTime(orderInfo.goodsReceiveDate)}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="备注:" label-width="60px">
              <span>{{orderInfo.goodsReceiveRemark}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="5" class="text-r">
            <el-button type="primary" :disabled="false" @click="printCode">打印条码</el-button>
          </el-col>
        </el-row>
        <el-row>
          <div>
            <common-table>
              <template slot="table">
                <el-table
                  ref="orderTable"
                  max-height="400"
                  stripe
                  :border="true"
                  tooltip-effect="light"
                  v-loading="isListLoading"
                  :data="tableData">
                  <!--<el-table-column type="selection" width="30"></el-table-column>-->
                  <el-table-column type="index" label="序号" width="50"></el-table-column>
                  <el-table-column prop="productBrand" label="品牌" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="productModel" label="型号" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="productName" label="货品名称" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="productStandard" label="规格" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="productColor" label="颜色"></el-table-column>
                  <el-table-column prop="receiveItemPack" label="包装形式" :formatter="PackFormatter"></el-table-column>
                  <el-table-column prop="productType" label="货品类型" :formatter="typeFormatter"></el-table-column>
                  <el-table-column prop="productItemUuid" label="子件" :formatter="itemFormatter" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="receiveItemCount" label="数量"></el-table-column>
                  <el-table-column prop="receiveItemRemark" label="备注" width="150" :show-overflow-tooltip="true"></el-table-column>
                  <!--<el-table-column prop="productMain" label="计件类型" min-width="100" :show-overflow-tooltip="true"></el-table-column>-->
                  <el-table-column label="操作" width="80" >
                    <template slot-scope="scope">
                      <el-button
                        :disabled="orderInfo.goodsReceviceAuditing == '2'"
                        @click.native.prevent="addCode(scope.row,scope.$index)"
                        type="text"
                        rules="small">
                        条码补打
                      </el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </template>
            </common-table>
          </div>
        </el-row>
      </el-form>
    </el-dialog>
    <!-- 二维码打印 -->
    <qr-code
      :show.sync="qrCodeVisible"
      :paramsList = "paramsList"
      :tableData = "tableData"
    />
  </div>
</template>

<script>
  import moment from 'moment'
  import CommonTable from '@/components/commonTable'
  import TemplateCreate from '@/views/Development/ProductTemplate/Components/TemplateCreate'
  import TemplateImport from '@/views/Development/ProductTemplate/Components/ImportTemplate'
  import { getAllProducetTemplateList,uploadTemplate } from '@/api/warehouse'
  import { getEntryOrderDetail, updateEntryOrder, deleteEntryOrder, createQrCode, addQrCode } from '@/api/order'
  import { getAllMerchantsList } from '@/api/common'
  import { setSession, getSession } from '@/utils/session'
  import QrCode from './QrCode'
  const InitProduct = {
    productBrand: '',
    productModel: '',
    productName: '',
    productStandard: '',
    productColor: '',
    receiveItemPack: '',
    receiveItemCount: '',
    productItems: [],
    receiveGoodsCount: '',
    productType: '',
    productRemarks: '',
    productUuid: ''
  }
  export default {
    name: 'OrderDetail',
    data () {
      return {
        isVisible: false,
        orderInfo: {},
        existModel: [],
        tableData: [],
        qrCodeVisible: false,
        paramsList: [],
        isListLoading: false
      }
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      selectedOrderInfo: {
        type: Object
      }
    },
    watch: {
      show (isShow) {
        if (isShow) {
          this.isVisible = isShow
          this.orderInfo = Object.assign({}, this.selectedOrderInfo)
          // this.getTemplate().then(() => {
          //   console.log(this.existModel)
            this.tableData = [].concat(this.orderInfo.goodsReceiveItems)
            // for (let i = 0; i < this.tableData.length; i++) {
            //   let productUuid = this.tableData[i].receiveItemProductId
            //   let result = this.existModel.find(item => {
            //     return item.productUuid === productUuid
            //   })
            //   this.$set(this.tableData, i, Object.assign(this.tableData[i],result))
            // }
          // })
        }
      }
    },
    methods: {
      // 获取模板列表model
      getTemplate () {
        return new Promise(resolve => {
          let params = {
            merchantUuid: this.orderInfo.goodsReceiveMerchantId
          }
          this.existModel = []
          this.isListLoading = true
          getAllProducetTemplateList(params).then(res => {
            this.isListLoading = false
            res.forEach(item => {
              delete item.productItemUuid
            })
            this.existModel = [].concat(res)
            resolve()
          })
        })
      },
      getMerchantName (id) {
        return this.merchantsList.find(item => item.userUuid === id).userName
      },
      cancel () {
        this.orderInfo = {}
        this.tableData =[]
        this.existModel = []
        this.$refs.orderInfo.resetFields()
        this.$emit('update:show', false)
        this.isVisible = false
      },
      PackFormatter (row) {
        return row.receiveItemPack === '1' ? '有包装' : row.receiveItemPack === '2' ? '无包装' : '其他'
      },
      typeFormatter (row) {
        return row.productType === '0' ? '小件' : row.productType === '1' ? '套件' : row.productType === '2' ? '单品' : '未知'
      },
      itemFormatter (row) {
        if (row.productType == '1' && row.productItems.length) {
          let productItem = row.productItems.find( item => {
            return row.productItemUuid === item.productItemUuid
          })
          if(productItem) {
            return productItem.productItemName
          } else {
            return ''
          }
        }
      },
      formatTime (goodsReceiveDate) {
        if (goodsReceiveDate) {
          return moment(goodsReceiveDate).utcOffset(8).format('YYYY-MM-DD')
        }
      },
      printCode () {
        let params = {
          orderAccount: [this.orderInfo.goodsReceiveUuid]
        }
        createQrCode(params).then( res => {
          this.paramsList = []
          this.paramsList = res
          this.qrCodeVisible = true
        })
      },
      addCode (row,index) {
        this.$confirm('提醒 条码补打会导致货品数量增加，确定要补打吗？', '提示', {
          type: 'warning'
        }).then(() => {
          this.$prompt('请输入补打数量', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            inputPattern: /^[1-9]{1}\d{0,4}$/,
            inputErrorMessage: '条码数量应在0-99999之间'
          }).then(({ value }) => {
            let params = {
              goodReceiveItemUuid: row.receiveItemUuid,
              count: value
            }
            addQrCode(params).then( res => {
              if (!res.error) {
                this.paramsList = []
                this.paramsList = res
                this.qrCodeVisible = true
                row.receiveItemCount = Number(row.receiveItemCount)+Number(value)
                this.$emit('updateCount',value,index)
              }
            })
          }).catch(() => {
          });
        }).catch(() => {})
      }
    },
    components: {
      QrCode,
      CommonTable,
      TemplateCreate,
      TemplateImport
    }
  }
</script>

<style lang="scss">

</style>



